@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
body {
  margin: 0;
  font-family: "Goldman", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.garage-door{background-color:transparent;width:100%;height:100%;position:relative;overflow:hidden}.garage-door-left,.garage-door-right{width:50%;height:100%;position:absolute;top:0}.garage-door-left{left:-50%}.garage-door-color-picker{position:absolute;bottom:10px;left:10px;text-shadow:0 0 10px rgba(255,255,255,0.75);display:flex;justify-content:center;align-items:center;max-width:45vh}.garage-door-color-picker label{margin-left:10px}.garage-door-right{right:-50%}.garage-door-unlock{width:100px;height:100px;border-radius:100%;box-shadow:0 0 3px 3px white;position:absolute;top:calc(50% - 100px/ 2);right:calc(100% - 100px/ 2);z-index:1;display:flex;justify-content:center;align-items:center;font-size:calc(100px * 0.7);color:rgba(255,255,255,0.5);cursor:pointer}.garage-door-icon{position:absolute;bottom:0;right:0;-webkit-filter:invert(100%) opacity(10%) blur(1px);filter:invert(100%) opacity(10%) blur(1px);height:45vw;width:45vw;max-width:512px;max-height:512px}.garage-door-content{background-color:transparent;position:absolute;width:100%;height:100%}.garage-door-info{position:absolute;width:100%;height:calc(50% - 100px / 2 - 20px);background-color:transparent;display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff;text-shadow:0 0 10px rgba(0,0,0,0.75);font-size:4vh}

